'use strict';

import preloader from "./modules/preloader.js";
import click from "./services/click.js";
import {
    hamburgerClick,
    requestClick,
    spaceSelectClick, spaceSelectListClick
} from "./services/clickFunctions.js";
import resize from "./services/resize.js";
import {
    changeFooterTitle,
    changeTelMail,
    changeTitleModal
} from "./services/changeDomElements.js";
import scroll from "./services/scroll.js";
import menuScroll from "./modules/menu.js";

window.addEventListener('DOMContentLoaded', function () {

    changeFooterTitle();
    changeTelMail();
    changeTitleModal();

    preloader();
    click(hamburgerClick, requestClick, spaceSelectClick, spaceSelectListClick);
    resize(changeFooterTitle, changeTelMail, changeTitleModal);
    scroll(() => menuScroll('modifikator'));

});